<template>
    <div id="app">
        <div class="app-overlay">
            <div class="nav-border">
                <nav>
                    <div class="nav-wrapper">
                        <router-link to="/" class="brand-logo"><img src="./assets/images/au-logo.png"></router-link>
                        <a href="#" data-target="mobile-nav" class="sidenav-trigger"><i class="fa fa-bars"></i></a>
                        <ul class="right hide-on-med-and-down desktop-nav">
                            <li><router-link to="/"><i class="fa fa-home"></i></router-link></li>
                            <li><router-link to="/about">About</router-link></li>
                            <li><router-link to="/projects">Projects</router-link></li>
                            <li><router-link to="/contact">Contact</router-link></li>
                        </ul>
                    </div>
                </nav>
            </div>

            <!-- // Display Content Here // -->
            <router-view/>

        </div>
        <ul class="sidenav" id="mobile-nav">
            <li><router-link to="/">Home</router-link></li>
            <li><router-link to="/about">About</router-link></li>
            <li><router-link to="/projects">Projects</router-link></li>
            <li><router-link to="/contact">Contact</router-link></li>
        </ul>
    </div>
</template>

<script>
  import './assets/font-awesome/css/all.css';

  export default {
    name: "App"
  }
</script>

<style lang="scss">
    $gradient: linear-gradient(to right, #8AC249, #00BCD4);

    html {
        height: 100%;
        width: 100%;
        background: url('assets/images/kc-background.jpg') center center no-repeat fixed;
        background-size: cover;
    }

    #app {
        height: 100%;

        .app-overlay {
            background: rgba(0, 0, 0, .75);
            position: fixed;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            overflow: auto;


            .nav-border {
                background: $gradient;
                background-size: 300% 300%;
                height: 108px;
                box-shadow: 0 0 10px 3px #000;
                animation: pulse 2.5s ease infinite;

                @keyframes pulse {
                    0% {
                        background-position: 0% 50%;
                    }
                    50% {
                        background-position: 100% 50%;
                    }
                    100% {
                        background-position: 0% 50%;
                    }
                }

                nav {
                    background: #263238;
                    height: 100px;
                    width: 100%;
                    line-height: 100px;
                    font-family: Futura, sans-serif;

                    .nav-wrapper {
                        padding: 0 40px;

                        .brand-logo {
                            img {
                                margin: 10px 0 0 0;
                                width: 75px;
                            }
                        }

                        .desktop-nav {
                            li {
                                a {
                                    font-size: 1.3rem;
                                    padding: 0 35px;
                                    transition: 1.25s;
                                    text-transform: uppercase;
                                    backface-visibility: hidden;

                                    &:hover {
                                        color: #00BCD4;
                                        transform: scale(1.05);
                                        backface-visibility: hidden;
                                        transition: .4s;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        .container {
            font-family: Futura, sans-serif;
            font-size: 16px;
            background: #263238BF;
            border-radius: 6px;
            color: #eceff1;
            margin-top: 40px;
            margin-bottom: 60px;
            border-bottom: 5px solid #8AC249BF;

            .content {
                padding: 20px;

                h1, h2, h3 {
                    &::before {
                        content: '< ';
                        color: #89DDFF;
                    }

                    &::after {
                        content: ' >';
                        color: #89DDFF;
                    }
                }

                h1 {
                    color: #C3E88D;
                    margin: 0;
                    font-size: 2.25rem;
                }

                h2 {
                    color: #F07178;
                    margin: 0;
                    font-size: 1.75rem;
                }

                h3 {
                    color: #FFCB6B;
                    margin: 0;
                    font-size: 1.25rem;

                    a {
                        color: #FFCB6B;
                    }
                }
            }
        }
    }


</style>



